<template>
    <main id="adminDisplay">
        <div style="margin-bottom:25px;">
            <div style="font-size:30px;text-align:left;padding-left:15px;padding-top:15px;">首页展示图<span
                    style="font-size:20px;"> *图片最佳像素比:320:121 图片格式:png</span></div>
            <hr>
            <div class="row">
                <!-- <div class="addPicDiv"></div> -->
                <div class="col-2" v-for="(homeImgUrl,index) in homeImgUrlList" :key="homeImgUrl"
                    style="margin-left:18px;">
                    <img :src="homeImgUrl" style="width:320px;height:121px;margin-right:15px;margin-bottom:10px;">
                    <div class="btn btn-warning btn-sm addPicDiv">
                        <input :id="'file_'+index" type="file" class="btn btn-default" style="opacity:0;"
                            @change="changeHomeImg('file_'+index,index,$event)" accept=".png">
                    </div>
                </div>
            </div>
            <hr>
        </div>

        <!-- <div>
            <div style="font-size:30px;text-align:left;padding-left:15px;padding-top:15px;">首页背景图<span
                    style="font-size:20px;"> *图片最佳像素比:1914:881 图片格式:png</span></div>
            <hr>
            <img src="../../assets/img/common/home_background.png" style="width: 478.5px;height:220.25px;">
        </div> -->

        <div style="margin-top:25px;">
            <button class="btn btn-success" style="margin-right:15px;" @click="submitImgForm">保存更改</button>
            <button class="btn btn-info" onclick="window.location.reload()">重置修改</button>
        </div>
    </main>
</template>

<script>
    export default {
        name: "adminDisplay",
        data() {
            return {
                homeImgUrlList: ["", "", "", "", ""],
                homeBackgroundUrl: "",
                files: []
            }
        },
        methods: {
            addImgUrlToList() {
                for (var i = 0; i < 5; ++i) {
                    this.homeImgUrlList[i] = this.getHomeImgUrl("home_0" + (i + 1) + ".png");
                }
            },
            changeHomeImg(sourceId, index, event) {
                var url = this.getFileUrl(sourceId);
                var file = event.target.files[0]; // (利用console.log输出看file文件对象)

                var newfile = new File([file], "home_0" + (index + 1), {
                    type: file.type
                });

                this.homeImgUrlList.splice(index, 1, url);

                this.files[index] = newfile;
            },
            submitImgForm() {
                var confi = confirm("确定保存以上更改");
                if (confi == true) {

                    var formData = new FormData();
                    for (let i = 0; i < this.files.length; i++) {
                        formData.append("files", this.files[i]);
                    }
                    this.axios({
                        url: "/api/yachtImg/admin/addHomeImg.action",
                        method: "post",
                        data: formData,
                        headers: {
                            "Content-Type": "multipart/form-data"
                        }
                    }).then((response) => {
                        var map = response.data;
                        if (map.code == 401) {
                            window.location.href = '/#/admin';
                        } else if (map.code == 200) {
                            if (map.data == null) {
                                this.clearYacht();
                                this.getYachts();
                                this.$emit("showAlert", true, "操作成功");
                            } else {
                                this.$emit("showAlert", true, map.data);
                            }
                        } else {
                            console.log(map);
                        }
                    }).catch((error) => {
                        console.log(error);
                        if (error.response.status == 401) { //无权限
                            window.location.href = '/#/admin';
                        }
                    });
                }
            },
            getFileUrl(sourceId) {
                var url;
                if (navigator.userAgent.indexOf("MSIE") >= 1) { // IE
                    url = document.getElementById(sourceId).value;
                } else if (navigator.userAgent.indexOf("Firefox") > 0) { // Firefox
                    url = window.URL.createObjectURL(document.getElementById(sourceId).files.item(0));
                } else if (navigator.userAgent.indexOf("Chrome") > 0) { // Chrome
                    url = window.URL.createObjectURL(document.getElementById(sourceId).files.item(0));
                }
                return url;
            },
            getFileByImgUrl(imgUrl, fileName) { //根据图片url地址获取file对象函数
                var _this = this;
                this.getImgToBase64(imgUrl, function (data) {
                    var blob = _this.dataUrlToBlob(data);
                    var file = _this.blobToFile(blob, fileName);
                    _this.files.push(file);
                    // return file;
                });
            },
            getImgToBase64(url, callback) { //将图片转换为Base64
                var canvas = document.createElement('canvas'),
                    ctx = canvas.getContext('2d'),
                    img = new Image;
                img.crossOrigin = 'Anonymous';
                img.onload = function () {
                    canvas.height = img.height;
                    canvas.width = img.width;
                    ctx.drawImage(img, 0, 0);
                    var dataUrl = canvas.toDataURL('image/png');
                    callback(dataUrl);
                    canvas = null;
                };
                img.src = url;
            },
            dataUrlToBlob(dataUrl) { //将base64转换为blob
                var arr = dataUrl.split(','),
                    mime = arr[0].match(/:(.*?);/)[1],
                    bstr = atob(arr[1]),
                    n = bstr.length,
                    u8arr = new Uint8Array(n);
                while (n--) {
                    u8arr[n] = bstr.charCodeAt(n);
                }
                return new Blob([u8arr], {
                    type: mime
                });
            },
            blobToFile(theBlob, fileName) { //将blob转换为file
                theBlob.lastModifiedDate = new Date();
                theBlob.name = fileName;
                return theBlob;
            },
        },
        created() {
            this.addImgUrlToList();
        }
    }
</script>

<style scoped>
    .addPicDiv {
        border: solid 1px;
        width: 307px;
        height: 50px;
        background-image: url('../../assets/img/common/addPic_02.png');
        background-size: 95%;
    }
</style>