<template>
    <div>
        <main id="adminYacht">
            <div class="row" style="margin-top:10px;margin-bottom:10px;margin-left:15px;margin-right:15px;">
                <div class="col-2">
                    <table style="white-space:nowrap;">
                        <tr>
                            <td rowspan="2">
                                <span style="float:left;">长度(米):</span>
                            </td>
                            <td>
                                <input type="text" class="form-control" placeholder="最小值" style="float:left;"
                                    v-model="minLength" onkeydown="if(event.keyCode==32){return false;}">
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <input type="text" class="form-control" placeholder="最大值" style="float:left;"
                                    v-model="maxLength" onkeydown="if(event.keyCode==32){return false;}">
                            </td>
                        </tr>
                    </table>
                </div>

                <div class="col-2">
                    <table style="white-space:nowrap;">
                        <tr>
                            <td rowspan="2">
                                <span style="float:left;">限载(人):</span>
                            </td>
                            <td>
                                <input type="number" class="form-control" placeholder="最小值" min="0" v-model="minMembers"
                                    onkeydown="if(event.keyCode==32){return false;}">
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <input type="number" class="form-control" placeholder="最大值" min="0" v-model="maxMembers"
                                    onkeydown="if(event.keyCode==32){return false;}">
                            </td>
                        </tr>
                    </table>
                </div>

                <div class="col-3">
                    <table style="white-space:nowrap;">
                        <tr>
                            <td>
                                <span style="float:left;">维护周期(分钟):</span>
                            </td>
                            <td>
                                <input type="number" class="form-control" min="0" v-model="yacht_c.maintenance"
                                    onkeydown="if(event.keyCode==32){return false;}">
                            </td>
                        </tr>
                        <tr>
                            <td>
                                游艇状态:
                            </td>
                            <td>
                                <select class="form-control" v-model="yacht_c.state">
                                    <option>-请选择状态-</option>
                                    <option>租赁中</option>
                                    <option>销售中</option>
                                    <option>已售出</option>
                                    <option>禁用状态</option>
                                </select>
                            </td>
                        </tr>
                    </table>
                </div>

                <div class="col-4" style="padding-top:20px;">
                    <div class="input-group">
                        <input type="text" class="form-control" placeholder="游艇号/名称/颜色/地址" v-model="condition">
                        <span class="input-group-btn">
                            <button class="btn btn-info" type="button" @click="getYachts('find')">检索</button>
                        </span>
                    </div>
                </div>
            </div>
            <div style="height:595px;">
                <table class="table" style="margin:auto">
                    <thead>
                        <tr>
                            <td>游艇号</td>
                            <td>名称</td>
                            <td>颜色</td>
                            <td>长度</td>
                            <td>限载人数</td>
                            <td>地址</td>
                            <td>出售价格</td>
                            <td>维护周期</td>
                            <td>包含套餐</td>
                            <td>游艇展示图</td>
                            <td>状态</td>
                            <td>操作<button class="btn btn-success btn-sm" style="margin-left:15px;" data-toggle="modal"
                                    data-target="#yachtModal" @click="clearYacht();modalTitle = '添加一艘游艇'">添加</button>
                            </td>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="yacht in yachtList" :key="yacht.id">
                            <td>{{yacht.id}}</td>
                            <td>{{yacht.name}}</td>
                            <td>{{yacht.color}}</td>
                            <td>{{yacht.length}} 米</td>
                            <td>{{yacht.members}}人</td>
                            <td>{{yacht.address}}</td>
                            <td>￥ {{(yacht.price/100)|NumFormat}}</td>
                            <td>{{yacht.maintenance}}分钟/{{(yacht.maintenance/60)|numFilter}}小时/{{(yacht.maintenance/1440)|numFilter}}天
                            </td>
                            <td>{{yacht.yachtSchemeList.length}}个</td>
                            <td>
                                <img :src="base64ToImgUrl(yacht.yachtImgList[0].img)" style="height:25px;">
                            </td>
                            <td>
                                <span v-if="yacht.state == 0">
                                    禁用状态
                                </span>
                                <span v-if="yacht.state == 1">
                                    租赁中
                                </span>
                                <span v-if="yacht.state == 2">
                                    销售中
                                </span>
                                <span v-if="yacht.state == 3">
                                    已售出
                                </span>
                            </td>
                            <td>
                                <button class="btn btn-info btn-sm" data-toggle="modal" data-target="#yachtModal"
                                    @click="getYachtById(yacht.id);modalTitle = '修改一艘游艇';">修改</button>
                                <button class="btn btn-danger btn-sm" @click="deleteYachtById(yacht.id);">删除</button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <div style="margin-top:2px;">
                <div>共{{allCount}}条记录, {{allPage}}页</div>
                <nav aria-label="Page navigation">
                    <ul class="pagination justify-content-center">
                        <li class="page-item" :class="{disabled: nowPage===1}">
                            <a href="#" aria-label="previous" class="page-link">
                                <span @click="nowPage--; getYachts();">上一页</span>
                            </a>
                        </li>
                        <li v-for="page in allPage" :key="page" class="page-item" :class="{active: nowPage===page}"><a
                                href="#" class="page-link" @click="nowPage = page; getYachts();"
                                v-if="page>nowPage-5&&page<nowPage+5">{{page}}</a></li>
                        <li class="page-item" :class="{disabled: nowPage===allPage}">
                            <a href="#" aria-label="Next" class="page-link">
                                <span @click="nowPage++; getYachts();">下一页</span>
                            </a>
                        </li>
                    </ul>
                </nav>
            </div>
        </main>

        <!-- 添加/修改游艇Modal begin -->
        <div id="yachtModal" class="modal fade bs-example-modal-lg" tabindex="-1" role="dialog"
            aria-labelledby="myModalLabel">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h4 class="modal-title" id="myModalLabel">{{modalTitle}}</h4>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span
                                aria-hidden="true">&times;</span></button>
                    </div>
                    <div class="modal-body row">
                        <div class="col-6">
                            <div class="form-group">
                                <label>游艇名称:</label>
                                <input type="text" class="form-control" v-model="yacht.name">
                            </div>
                            <div class="form-group">
                                <label>颜色:</label>
                                <input type="text" class="form-control" v-model="yacht.color">
                            </div>
                            <div class="form-group">
                                <label>长度(米):</label>
                                <input type="text" class="form-control" v-model="yacht.length"
                                    onkeydown="if(event.keyCode==32){return false;}">
                            </div>
                            <div class="form-group">
                                <label>限载(人):</label>
                                <input type="number" class="form-control" v-model="yacht.members"
                                    onkeydown="if(event.keyCode==32){return false;}">
                            </div>
                            <div class="form-group">
                                <label>地址:</label>
                                <input type="text" class="form-control" v-model="yacht.address">
                            </div>
                            <div class="form-group">
                                <label>出售价格(￥):</label>
                                <input type="text" class="form-control" v-model="yacht.price"
                                    onkeydown="if(event.keyCode==32){return false;}">
                            </div>
                            <div class="form-group">
                                <label>维护周期(分钟):</label>
                                <input type="number" class="form-control" v-model="yacht.maintenance"
                                    onkeydown="if(event.keyCode==32){return false;}">
                            </div>
                            <div class="form-group">
                                <label>状态:</label>
                                <select class="form-control" v-model="yacht.state">
                                    <option>禁用状态</option>
                                    <option>租赁中</option>
                                    <option>销售中</option>
                                    <option>已售出</option>
                                </select>
                            </div>
                        </div>
                        <div class="col-6">
                            <h3>设置展示图:</h3>
                            <span style="font-size:13px;">图片最佳比例:75:43 *注:所有图片大小必须小于100M</span>
                            <hr>
                            <table>
                                <tr>
                                    <td style="width:30px;">主图</td>
                                    <td>
                                        <div v-if="mainImgUrl == ''" class="addPicDiv">
                                            <input type="file" id="mainImgFile" @change="preImg('mainImgFile',$event)"
                                                accept=".png"
                                                style="position：relative;height:60px;width:60px;text-align:center;margin-left:-14px;margin-top:-7px;opacity:0;cursor:pointer;">
                                        </div>
                                        <img v-if="mainImgUrl != ''" :src="mainImgUrl"
                                            style="width:60px;height:60px;margin-left:1px;"
                                            @click="detachImg('mainImg')">
                                    </td>
                                </tr>
                                <hr>
                                <tr>
                                    <td>副图</td>
                                    <td>
                                        <div class="addPicDiv">
                                            <input id="secondaryImgFile" type="file" accept=".png"
                                                @change="preImg('secondaryImgFile',$event)"
                                                style="position：relative;height:60px;width:60px;text-align:center;margin-left:-14px;margin-top:-7px;opacity:0;cursor:pointer;">
                                        </div>
                                        <img v-for="secondaryImgUrl in secondaryImgUrlList" :src="secondaryImgUrl"
                                            :key="secondaryImgUrl" style="width:60px;height:60px;margin-left:1px;"
                                            @click="detachImg('secondaryImg',secondaryImgUrl)">
                                    </td>
                                </tr>
                            </table>
                            <hr>
                            <h3>设置套餐:</h3>
                            <table>
                                <tr>
                                    <td>包艇时长(小时)</td>
                                    <td>价格(￥)</td>
                                </tr>
                                <tr v-for="yachtScheme in yachtSchemeList" :key="yachtScheme.id">
                                    <td><input class="form-control" type="number" v-model="yachtScheme.time"
                                            onkeydown="if(event.keyCode==32){return false;}"></td>
                                    <td><input class="form-control" type="text" v-model="yachtScheme.price"
                                            onkeydown="if(event.keyCode==32){return false;}"></td>
                                </tr>
                                <tr>
                                    <button class="btn btn-success glyphicon glyphicon-plus"
                                        @click="addScheme"></button>
                                    <button :disabled="this.yachtSchemeList.length==0"
                                        class="btn btn-danger glyphicon glyphicon-minus" @click="removeScheme"></button>
                                </tr>
                            </table>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-success" @click="addYacht"
                            v-if="modalTitle == '添加一艘游艇'">添加</button>
                        <button type="button" class="btn btn-primary" @click="updateYacht"
                            v-if="modalTitle == '修改一艘游艇'">修改</button>
                    </div>
                </div>
            </div>
        </div>
        <!-- 添加/修改游艇Modal end -->
    </div>
</template>

<script>
    export default {
        name: 'adminYacht',
        data() {
            return {
                yacht: {
                    id: "",
                    name: "",
                    color: "",
                    length: "",
                    members: "",
                    address: "",
                    maintenance: "",
                    state: "禁用状态",
                    yachtImgList: [],
                    yachtSchemeList: []
                },
                yacht_c: { //用来存放筛选条件
                    id: "",
                    name: "",
                    color: "",
                    length: "",
                    members: "",
                    address: "",
                    maintenance: "",
                    state: "-请选择状态-"
                },
                yachtScheme: {
                    id: "",
                    yachtId: "",
                    time: "",
                    price: ""
                },
                yachtImg: {
                    id: "",
                    yachtId: "",
                    img: ""
                },
                yachtImgList: [],
                yachtSchemeList: [],
                yachtList: [],
                mainImgUrl: "", //主图url,url用于在页面回显选中的图片
                secondaryImgUrlList: [], //副图url数组,url用于在页面回显选中的图片
                files: [], //存放真正的文件对象
                modalTitle: "添加一艘游艇",
                condition: "",
                maxLength: "",
                minLength: "",
                maxMembers: "",
                minMembers: "",
                nowPage: 1, //当前页数
                allPage: 0, //总页数
                counts: 9, //一页显示条数
                allCount: 0, //总记录数
                selectUseTime: "",
                selectCreated: "",
            }
        },
        methods: {
            getYachts(button) { //分页条件查询游艇信息信息
                var state = "";
                if (this.yacht_c.state == "-请选择状态-") {
                    state = "";
                } else if (this.yacht_c.state == "租赁中") {
                    state = 1;
                } else if (this.yacht_c.state == "禁用状态") {
                    state = 0;
                } else if (this.yacht_c.state == "销售中") {
                    state = 2;
                } else if (this.yacht_c.state == "已售出") {
                    state = 3;
                }
                if (button == "find") { //判断是 "切换页数" 触发该函数,还是 "点击检索" 触发该函数,点击检索会自动切换到第一页
                    this.nowPage = 1;
                }
                this.axios({
                    method: "post",
                    url: "/api/yacht/admin/getYachts.action",
                    data: {
                        condition: this.condition,
                        minLength: this.minLength,
                        maxLength: this.maxLength,
                        minMembers: this.minMembers,
                        maxMembers: this.maxMembers,
                        state,
                        maintenance: this.yacht_c.maintenance,
                        page: this.nowPage,
                        counts: this.counts
                    },
                    transformRequest: [function (data) { //传递表单信息的post请求
                        // Do whatever you want to transform the data
                        let ret = '';
                        for (let it in data) {
                            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&';
                        }
                        return ret;
                    }],
                }).then((response) => {
                    var map = response.data;
                    if (map.code == 401) { //未登录
                        window.location.href = '/#/admin';
                    } else if (map.code == 200) {
                        var data = JSON.parse(map.data);
                        if (data != null) {
                            var yachtJson = data; //返回一个json字符串数组,json[0]是结果集list的json字符串格式,json[1]是一个整数即总记录数
                            this.allCount = yachtJson[1]; //取出总记录数
                            this.allPage = parseInt(this.allCount / (this.counts + 1)) + 1; //计算总页数
                            if (this.nowPage > this.allPage) { //判断当前页是否大于最大页数,防止用户筛选完后页数减少,而当前页不显示记录的情况
                                this.nowPage = 1;
                                this.getConsumes();
                            } else {
                                this.yachtList = JSON.parse(yachtJson[0]); //取出分页条件查询结果集渲染页面
                            }
                        }
                    } else {
                        console.log(map);
                    }
                }).catch((error) => {
                    console.log(error);
                    if (error.response.status == 401) { //无权限
                        window.location.href = '/#/admin';
                    }
                });
            },
            getFileUrl(sourceId) {
                var url;
                if (navigator.userAgent.indexOf("MSIE") >= 1) { // IE
                    url = document.getElementById(sourceId).value;
                } else if (navigator.userAgent.indexOf("Firefox") > 0) { // Firefox
                    url = window.URL.createObjectURL(document.getElementById(sourceId).files.item(0));
                } else if (navigator.userAgent.indexOf("Chrome") > 0) { // Chrome
                    url = window.URL.createObjectURL(document.getElementById(sourceId).files.item(0));
                }
                return url;
            },
            preImg(sourceId, event) {
                var url = this.getFileUrl(sourceId);
                var file = event.target.files[0]; // (利用console.log输出看file文件对象)

                if (sourceId == "mainImgFile") { //传入主图文件
                    this.mainImgUrl = url;
                    this.files.unshift(file);
                } else if (sourceId == "secondaryImgFile") { //传入副图文件
                    this.secondaryImgUrlList.push(url);
                    this.files.push(file);
                }
            },
            detachImg(img, url) { //img两个参数:mainImg和secondaryImg     url:待移除副图的url
                if (img == "secondaryImg") {
                    for (var i = 0; i < this.secondaryImgUrlList.length; ++i) {
                        if (this.secondaryImgUrlList[i] == url) {
                            this.secondaryImgUrlList.splice(i, 1);
                            if (this.mainImgUrl != "") { //主图在imgFileList对象数组里存在,下标要减一
                                this.files.splice(i + 1, 1);
                            } else {
                                this.files.splice(i, 1);
                            }
                        }
                    }
                } else if (img == "mainImg") {
                    this.mainImgUrl = "";
                    this.files.splice(0, 1);
                }
            },
            addScheme() { //添加套餐
                var flag = true; //标记是否有空的套餐
                for (var i = 0; i < this.yachtSchemeList.length; ++i) {
                    if (this.yachtSchemeList[i].time == "" || this.yachtSchemeList[i].price == "") {
                        flag = false;
                    }
                }
                if (this.yachtSchemeList != "" && flag) {
                    this.yachtSchemeList.push({
                        time: "",
                        price: ""
                    });
                } else if (this.yachtSchemeList == "") {
                    this.yachtSchemeList.push({
                        time: "",
                        price: ""
                    });
                }
            },
            removeScheme() { //移除最末尾的套餐
                if (this.yachtSchemeList.length > 0) {
                    this.yachtSchemeList.splice(this.yachtSchemeList.length - 1, 1);
                }
            },
            addYacht() { //添加一辆游艇
                var confi = confirm("确认此操作?");
                if (confi == true) {
                    if (this.yacht.name == "") {
                        this.$emit("showAlert", true, "请填写游艇名称");
                    } else if (this.yacht.color == "") {
                        this.$emit("showAlert", true, "请填写游艇颜色");
                    } else if (!/^(?!0+(?:\.0+)?$)(?:[0-9]\d*|0)(?:\.\d{1,2})?$/.test(this.yacht
                        .length)) { //正则:包括零和正数的两位小数
                        this.$emit("showAlert", true, "请填写正确游艇长度(最多支持两位小数)");
                    } else if (!(/(^[0-9]\d*$)/.test(this.yacht.members))) { //正则:大于等于零的正整数
                        this.$emit("showAlert", true, "请填写正确的限载人数(大于等于零的整数)");
                    } else if (this.yacht.address == "") {
                        this.$emit("showAlert", true, "请填写游艇地址");
                    } else if (!/^(?!0+(?:\.0+)?$)(?:[1-9]\d*|0)(?:\.\d{1,2})?$/.test(this.yacht
                        .price)) { //正则:包括零和正数的两位小数
                        this.$emit("showAlert", true, "请填写正确的游艇价格(最多支持两位小数)");
                    } else if (!(/(^[0-9]\d*$)/.test(this.yacht.maintenance))) { //正则:大于等于零的正整数
                        this.$emit("showAlert", true, "请填写正确的维护周期(大于等于零的整数)");
                    } else if (this.mainImgUrl == "" || this.secondaryImgUrlList.length <= 0) {
                        this.$emit("showAlert", true, "请至少选择一张主图和一张副图");
                    } else if (this.yacht.state == "租赁中" && this.yachtSchemeList.length <= 0) {
                        this.$emit("showAlert", true, "该游艇用于租赁,必须至少包含一个套餐");
                    } else {
                        var flag = true;
                        if (this.yachtSchemeList.length > 0) {
                            for (var i = 0; i < this.yachtSchemeList.length; ++i) {
                                if (this.yachtSchemeList[i].time == "" || this.yachtSchemeList[i].price == "") {
                                    this.$emit("showAlert", true, "请正确设置套餐");
                                    flag = false;
                                    break;
                                } else if (!(/(^[0-9]\d*$)/.test(this.yachtSchemeList[i].time))) {
                                    this.$emit("showAlert", true, "请正确设置套餐包艇时长(大于等于零的整数)");
                                    flag = false;
                                    break;
                                } else if (!/^(?!0+(?:\.0+)?$)(?:[0-9]\d*|0)(?:\.\d{1,2})?$/.test(this.yachtSchemeList[
                                            i]
                                        .price)) {
                                    this.$emit("showAlert", true, "请正确设置套餐价格(最多支持两位小数)");
                                    flag = false;
                                    break;
                                }
                            }
                        }
                        if (flag) {
                            $('#yachtModal').modal('hide'); //关闭添加游艇模态框
                            if (this.yacht.state == "租赁中") {
                                this.yacht.state = 1;
                            } else if (this.yacht.state == "销售中") {
                                this.yacht.state = 2;
                            } else if (this.yacht.state == "禁用状态") {
                                this.yacht.state = 0;
                            } else if (this.yacht.state == "已售出") {
                                this.yacht.state = 3;
                            }
                            this.yacht.price = this.yacht.price * 100;
                            for (var i = 0; i < this.yachtSchemeList.length; ++i) {
                                this.yachtSchemeList[i].price = this.yachtSchemeList[i].price * 100; //转化成  分  为单位
                            }
                            this.secondaryImgUrlList.unshift(this.mainImgUrl); //合并主图和副图的url赋值给yachtImgList
                            for (var i = 0; i < this.secondaryImgUrlList.length; ++i) { //将合并后的url字符串压入this.yachtImgList
                                this.yachtImgList.push({
                                    id: "",
                                    yachtId: "",
                                    img: this.secondaryImgUrlList[i].toString()
                                });
                            }
                            this.yacht.yachtSchemeList = this.yachtSchemeList;
                            this.yacht.yachtImgList = this.yachtImgList;

                            this.axios({
                                url: "/api/yacht/admin/addYacht.action",
                                method: "post",
                                data: this.yacht
                            }).then((response) => {
                                var map = response.data;
                                if (map.code == 401) {
                                    window.location.href = '/#/admin';
                                } else if (map.code == 200) {
                                    if (map.data == "服务器错误") {
                                        this.$emit("showAlert", true, "服务器异常");
                                    } else { //data返回一个最新添加的游艇id
                                        this.addImgFile(map.data); //添加图片到服务器
                                    }
                                }
                            }).catch((error) => {
                                console.log(error);
                                if (error.response.status == 401) { //无权限
                                    window.location.href = '/#/admin';
                                }
                            });
                        }

                    }
                }
            },
            updateYacht() { //修改一艘游艇
                var confi = confirm("确认此操作?");
                if (confi == true) {
                    if (this.yacht.name == "") {
                        this.$emit("showAlert", true, "请填写游艇名称");
                    } else if (this.yacht.color == "") {
                        this.$emit("showAlert", true, "请填写游艇颜色");
                    } else if (!/^(?!0+(?:\.0+)?$)(?:[0-9]\d*|0)(?:\.\d{1,2})?$/.test(this.yacht
                            .length)) { //正则:包括零和正数的两位小数
                        this.$emit("showAlert", true, "请填写正确游艇长度(最多支持两位小数)");
                    } else if (!(/(^[0-9]\d*$)/.test(this.yacht.members))) { //正则:大于等于零的正整数
                        this.$emit("showAlert", true, "请填写正确的限载人数(大于等于零的整数)");
                    } else if (this.yacht.address == "") {
                        this.$emit("showAlert", true, "请填写游艇地址");
                    } else if (!/^(?!0+(?:\.0+)?$)(?:[1-9]\d*|0)(?:\.\d{1,2})?$/.test(this.yacht
                            .price)) { //正则:包括零和正数的两位小数
                        this.$emit("showAlert", true, "请填写正确的游艇价格(最多支持两位小数)");
                    } else if (!(/(^[0-9]\d*$)/.test(this.yacht.maintenance))) { //正则:大于等于零的正整数
                        this.$emit("showAlert", true, "请填写正确的维护周期(大于等于零的整数)");
                    } else if (this.mainImgUrl == "" || this.secondaryImgUrlList.length <= 0) {
                        this.$emit("showAlert", true, "请至少选择一张主图和一张副图");
                    } else if (this.yacht.state == "租赁中" && this.yachtSchemeList.length <= 0) {
                        this.$emit("showAlert", true, "该游艇用于租赁,必须至少包含一个套餐");
                    } else {
                        var flag = true;
                        if (this.yachtSchemeList.length > 0) {
                            for (var i = 0; i < this.yachtSchemeList.length; ++i) {
                                if (this.yachtSchemeList[i].time == "" || this.yachtSchemeList[i].price == "") {
                                    this.$emit("showAlert", true, "请正确设置套餐");
                                    flag = false;
                                    break;
                                } else if (!(/(^[0-9]\d*$)/.test(this.yachtSchemeList[i].time))) {
                                    this.$emit("showAlert", true, "请正确设置套餐包艇时长(大于等于零的整数)");
                                    flag = false;
                                    break;
                                } else if (!/^(?!0+(?:\.0+)?$)(?:[0-9]\d*|0)(?:\.\d{1,2})?$/.test(this.yachtSchemeList[
                                            i]
                                        .price)) {
                                    this.$emit("showAlert", true, "请正确设置套餐价格(最多支持两位小数)");
                                    flag = false;
                                    break;
                                }
                            }
                        }
                        if (flag) {
                            $('#yachtModal').modal('hide'); //关闭添加游艇模态框
                            if (this.yacht.state == "租赁中") {
                                this.yacht.state = 1;
                            } else if (this.yacht.state == "销售中") {
                                this.yacht.state = 2;
                            } else if (this.yacht.state == "禁用状态") {
                                this.yacht.state = 0;
                            } else if (this.yacht.state == "已售出") {
                                this.yacht.state = 3;
                            }
                            this.yacht.price = this.yacht.price * 100;
                            for (var i = 0; i < this.yachtSchemeList.length; ++i) {
                                this.yachtSchemeList[i].price = this.yachtSchemeList[i].price * 100; //转化成  分  为单位
                            }
                            this.secondaryImgUrlList.unshift(this.mainImgUrl); //合并主图和副图的url赋值给yachtImgList
                            for (var i = 0; i < this.secondaryImgUrlList.length; ++i) { //将合并后的url字符串压入this.yachtImgList
                                this.yachtImgList.push({
                                    id: "",
                                    yachtId: "",
                                    img: this.secondaryImgUrlList[i].toString()
                                });
                            }
                            this.yacht.yachtSchemeList = this.yachtSchemeList;
                            this.yacht.yachtImgList = this.yachtImgList;

                            this.axios({
                                url: "/api/yacht/admin/updateYacht.action",
                                method: "post",
                                data: this.yacht
                            }).then((response) => {
                                var map = response.data;
                                if (map.code == 401) {
                                    window.location.href = '/#/admin';
                                } else if (map.code == 200) {
                                    if (map.data == "服务器错误") {
                                        this.$emit("showAlert", true, "服务器异常");
                                    } else { //data返回一个最新添加的游艇id
                                        this.files.sort(function (a, b) {
                                            if (a.name < b.name) {
                                                return -1;
                                            } else if (a.name == b.name) {
                                                return 0;
                                            } else {
                                                return 1;
                                            }
                                        });
                                        console.log(this.files);
                                        this.addImgFile(map.data); //添加/修改图片到服务器
                                    }
                                } else {
                                    console.log(map);
                                }
                            }).catch((error) => {
                                console.log(error);
                                if (error.response.status == 401) { //无权限
                                    window.location.href = '/#/admin';
                                }
                            });
                        }

                    }
                }
            },
            addImgFile(yachtId) { //上传图片到服务器
                var formData = new FormData();

                for (let i = 0; i < this.files.length; i++) {
                    formData.append("files", this.files[i]);
                }
                formData.append("yachtId", yachtId);

                this.axios({
                    url: "/api/yachtImg/admin/addYachtImg.action",
                    method: "post",
                    data: formData,
                    headers: {
                        "Content-Type": "multipart/form-data"
                    }
                }).then((response) => {
                    var map = response.data;
                    if (map.code == 401) {
                        window.location.href = '/#/admin';
                    } else if (map.code == 200) {
                        if (map.data == null) {
                            this.clearYacht();
                            this.getYachts();
                            this.$emit("showAlert", true, "操作成功");
                        } else {
                            this.$emit("showAlert", true, map.data);
                        }
                    } else {
                        console.log(map);
                    }
                }).catch((error) => {
                    console.log(error);
                    if (error.response.status == 401) { //无权限
                        window.location.href = '/#/admin';
                    }
                });
            },
            deleteYachtById(yachtId) { //根据游艇id删除一艘游艇    
                if (yachtId == "" || yachtId == null) {
                    this.$emit("showAlert", true, "参数传输错误");
                } else {
                    var confi = confirm("确定删除该游艇?");
                    if (confi == true) {
                        this.axios({
                            url: "/api/yacht/admin/deleteYachtById.action",
                            method: "get",
                            params: {
                                yachtId
                            }
                        }).then((response) => {
                            var map = response.data;
                            if (map.code == 401) {
                                window.location.href = '/#/admin';
                            } else if (map.code == 200) {
                                if (map.data == null) {
                                    this.$emit("showAlert", true, "操作成功");
                                    this.clearYacht();
                                    this.getYachts();
                                } else {
                                    this.$emit("showAlert", true, map.data);
                                }
                            } else {
                                console.log(map);
                            }
                        }).catch((error) => {
                            console.log(error);
                            if (error.response.status == 401) { //无权限
                                window.location.href = '/#/admin';
                            }
                        });
                    }
                }
            },
            getYachtById(yachtId) { //根据游艇id回显游艇信息到模态框,用于修改时回显数据
                this.clearYacht();
                if (yachtId != null && yachtId != "") {
                    this.axios({
                        url: "/api/yacht/getYachtMessageById.action",
                        method: "get",
                        params: {
                            "id": yachtId
                        }
                    }).then((response) => {
                        var map = response.data;
                        if (map.code == 200) {
                            var data = JSON.parse(map.data);
                            if (data != null) {
                                var yachtJson = data[0];
                                var yachtSchemeListJson = data[1];
                                this.yacht = JSON.parse(yachtJson);
                                if (this.yacht.state == 0) {
                                    this.yacht.state = "禁用状态";
                                } else if (this.yacht.state == 1) {
                                    this.yacht.state = "租赁中";
                                } else if (this.yacht.state == 2) {
                                    this.yacht.state = "销售中";
                                } else if (this.yacht.state == 3) {
                                    this.yacht.state = "已售出";
                                }
                                this.yacht.price = this.yacht.price / 100;
                                this.yachtSchemeList = JSON.parse(yachtSchemeListJson);

                                for (var i = 0; i < this.yachtSchemeList.length; ++i) {
                                    this.yachtSchemeList[i].price = this.yachtSchemeList[i].price / 100;
                                }

                                //设置展示图
                                this.mainImgUrl = this.base64ToImgUrl(this.yacht.yachtImgList[0].img);

                                for (let i = 0; i < this.yacht.yachtImgList.length; ++i) {
                                    if (i > 0) { //副图展示路径
                                        this.secondaryImgUrlList.push(this.base64ToImgUrl(this.yacht
                                            .yachtImgList[i]
                                            .img));
                                    }
                                    this.getFileByImgUrl(this.base64ToImgUrl(this.yacht.yachtImgList[i]
                                        .img), i); //存入file对象
                                }
                            } else {
                                this.$emit("showAlert", true, map.data);
                            }
                        } else {
                            console.log(map);
                        }
                    }).catch((error) => {
                        console.log(error);
                    });
                }
            },
            getFileByImgUrl(imgUrl, fileName) { //根据图片url地址获取file对象函数
                var _this = this;
                var file;
                this.getImgToBase64(imgUrl, function (data) {
                    var blob = _this.dataUrlToBlob(data);
                    var file = _this.blobToFile(blob, fileName);
                    _this.files.push(file);
                });
            },
            getImgToBase64(url, callback) { //将图片转换为Base64
                var canvas = document.createElement('canvas'),
                    ctx = canvas.getContext('2d'),
                    img = new Image;
                img.crossOrigin = 'Anonymous';
                img.onload = function () {
                    canvas.height = img.height;
                    canvas.width = img.width;
                    ctx.drawImage(img, 0, 0);
                    var dataUrl = canvas.toDataURL('image/png');
                    callback(dataUrl);
                    canvas = null;
                };
                img.src = url;
            },
            dataUrlToBlob(dataUrl) { //将base64转换为blob
                var arr = dataUrl.split(','),
                    mime = arr[0].match(/:(.*?);/)[1],
                    bstr = atob(arr[1]),
                    n = bstr.length,
                    u8arr = new Uint8Array(n);
                while (n--) {
                    u8arr[n] = bstr.charCodeAt(n);
                }
                return new Blob([u8arr], {
                    type: mime
                });
            },
            blobToFile(theBlob, fileName) { //将blob转换为file
                theBlob.lastModifiedDate = new Date();
                theBlob.name = fileName;
                return theBlob;
            },
            base64ToImgUrl(base64) {
                //传入base64字符串,获取对应图片url地址(仅限客户端访问)
                //base64字符串格式:  data:image/png;base64,iVBORw0KGgoAAAANSUhEUgA...
                var arr = base64.split(','),
                    mime = arr[0].match(/:(.*?);/)[1],
                    bstr = atob(arr[1]),
                    n = bstr.length,
                    u8arr = new Uint8Array(n);
                while (n--) {
                    u8arr[n] = bstr.charCodeAt(n);
                }
                var blob = new Blob([u8arr], {
                    type: mime
                });
                var imageUrl = (window.URL || window.webkitURL).createObjectURL(blob);
                return imageUrl;
            },
            clearYacht() { //清空暂用值
                this.yacht.id = "";
                this.yacht.name = "";
                this.yacht.color = "";
                this.yacht.length = "";
                this.yacht.members = "";
                this.yacht.address = "";
                this.yacht.maintenance = "";
                this.yacht.price = "";
                this.yacht.state = "禁用状态";
                this.yacht.yachtImgList = [];
                this.yacht.yachtSchemeList = [];
                this.yachtSchemeList = [];
                this.yachtImgList = [];

                this.mainImgUrl = "";
                this.secondaryImgUrlList = [];
                this.files = [];
            }
        },
        mounted() {
            this.getYachts();
        },
        created() {
            // this.$emit("ifAdmin");
        },
        filters: {
            NumFormat(value) { //三位一逗号
                if (!value) return '0.00';

                /*原来用的是Number(value).toFixed(0)，这样取整时有问题，例如0.51取整之后为1，感谢Nils指正*/
                var intPart = Number(value) | 0; //获取整数部分
                var intPartFormat = intPart.toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,'); //将整数部分逢三一断

                var floatPart = ".00"; //预定义小数部分
                value = value.toString();
                var value2Array = value.split(".");

                //=2表示数据有小数位
                if (value2Array.length == 2) {
                    floatPart = value2Array[1].toString(); //拿到小数部分

                    if (floatPart.length == 1) { //补0,实际上用不着
                        return intPartFormat + "." + floatPart + '0';
                    } else {
                        return intPartFormat + "." + floatPart;
                    }

                } else {
                    return intPartFormat + floatPart;
                }
            },
            numFilter(value) {
                // 截取当前数据到小数点后两位
                let realVal = parseFloat(value).toFixed(1)
                return realVal
            }
        }
    }
</script>

