/*
管理员 路由器对象模块
*/

import Vue from 'vue';
import VueRouter from 'vue-router';


//声明使用插件
Vue.use(VueRouter);

import AdminMember from '../../views/admin/AdminMember.vue';
import AdminYacht from '../../views/admin/AdminYacht.vue';
import AdminOrder from '../../views/admin/AdminOrder.vue';
import AdminDisplay from '../../views/admin/AdminDisplay.vue';

let router = new VueRouter({
    routes: [{
            path: '/adminMember',
            component: AdminMember
        }, {
            path: '/adminYacht',
            component: AdminYacht
        }, {
            path: '/adminOrder',
            component: AdminOrder
        },{
            path:'/adminDisplay',
            component: AdminDisplay
        },
        {
            path: '/',
            redirect: '/adminMember'
        }
    ]
});

export default router