<template>
    <div>
        <main id="adminMember">
            <div class="row" style="margin-top:10px;margin-bottom:10px;margin-left:15px;margin-right:15px;">
                <table style="width:100%;white-space:nowrap;">
                    <tr>
                        <td>性别:</td>
                        <td style="padding-right:55px;">
                            <select class="form-control" v-model="member_c.sex">
                                <option>-请选择用户性别-</option>
                                <option>男</option>
                                <option>女</option>
                            </select>
                        </td>
                        <td>级别:</td>
                        <td style="padding-right:55px;">
                            <select class="form-control" v-model="member_c.level">
                                <option>-请选择用户级别-</option>
                                <option>普通会员</option>
                                <option>管理员</option>
                            </select>
                        </td>
                        <td style="width:75px;text-align:right;padding-top:7px;">
                            <label for="exampleInputName2">年龄: </label>
                        </td>
                        <td style="width:105px;">
                            <input type="number" class="form-control" placeholder="最小值" min="0" v-model="minAge"
                                onkeydown="if(event.keyCode==32){return false;}">
                        </td>
                        <td>~</td>
                        <td style="width:105px;">
                            <input type="number" class="form-control" placeholder="最大值" min="0" v-model="maxAge"
                                onkeydown="if(event.keyCode==32){return false;}">
                        </td>
                        <td style="width:600px;padding-left:95px;" rowspan="2">
                            <div class="input-group">
                                <input type="text" class="form-control" placeholder="会员号/姓名/手机号" v-model="condition">
                                <span class="input-group-btn">
                                    <button class="btn btn-info" type="button" @click="getMembers('find')">检索</button>
                                </span>
                            </div>
                        </td>
                    </tr>
                </table>
            </div>
            <div style="height:635px;">
                <table class="table" style="margin:auto">
                    <thead>
                        <tr>
                            <td>会员号</td>
                            <td>姓名</td>
                            <td>手机号/管理员账号</td>
                            <td>性别</td>
                            <td>年龄</td>
                            <td>级别</td>
                            <td>操作
                                <button class="btn btn-success btn-sm" style="margin-left:15px;" data-toggle="modal"
                                    data-target="#memberModal" @click="clearMember();modalTitle = '添加一名用户'">添加</button>
                            </td>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="member in memberList" :key="member.id">
                            <td>{{member.id}}</td>
                            <td>{{member.name}}</td>
                            <td>{{member.tel}}</td>
                            <td>{{member.sex==1?'女':'男'}}</td>
                            <td>{{member.age}}</td>
                            <td>{{member.level==1?'管理员':'普通会员'}}</td>
                            <td>
                                <button class="btn btn-info btn-sm" data-toggle="modal" data-target="#memberModal"
                                    @click="getMemberById(member.id);modalTitle = '修改一名用户'">修改</button>
                                <button class="btn btn-danger btn-sm" @click="deleteMemberById(member.id)">删除</button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <div style="margin-top:2px;">
                <div>共{{allCount}}条记录, {{allPage}}页</div>
                <nav aria-label="Page navigation">
                    <ul class="pagination justify-content-center">
                        <li class="page-item" :class="{disabled: nowPage===1}">
                            <a href="#" aria-label="previous" class="page-link">
                                <span @click="nowPage--; getYachts();">上一页</span>
                            </a>
                        </li>
                        <li v-for="page in allPage" :key="page" class="page-item" :class="{active: nowPage===page}"><a
                                href="#" class="page-link" @click="nowPage = page; getYachts();"
                                v-if="page>nowPage-5&&page<nowPage+5">{{page}}</a></li>
                        <li class="page-item" :class="{disabled: nowPage===allPage}">
                            <a href="#" aria-label="Next" class="page-link">
                                <span @click="nowPage++; getYachts();">下一页</span>
                            </a>
                        </li>
                    </ul>
                </nav>
            </div>
        </main>

        <!-- 添加/修改会员Modal begin -->
        <div id="memberModal" class="modal fade" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h4 class="modal-title" id="myModalLabel">{{modalTitle}}</h4>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span
                                aria-hidden="true">&times;</span></button>
                    </div>
                    <div class="modal-body">
                        <div class="form-group" v-if="modalTitle == '修改一名用户'">
                            <label>会员号:</label>
                            <input disabled type="text" class="form-control" v-model="member.id">
                        </div>
                        <div class="form-group">
                            <label>级别:</label>
                            <select class="form-control" v-model="member.level" :disabled="modalTitle == '修改一名用户'">
                                <option>普通会员</option>
                                <option>管理员</option>
                            </select>
                        </div>
                        <div class="form-group">
                            <label v-if="member.level == '普通会员'">手机号:</label>
                            <label v-if="member.level == '管理员'">管理员账号:</label>
                            <input type="text" class="form-control" v-model="member.tel"
                                :disabled="modalTitle == '修改一名用户'" onkeydown="if(event.keyCode==32){return false;}">
                        </div>
                        <div class="form-group">
                            <label>姓名:</label>
                            <input type="text" class="form-control" v-model="member.name">
                        </div>
                        <div class="form-group">
                            <label>性别:</label>
                            <div class="radio">
                                <label class="radio-inline">
                                    <input type="radio" name="sex" value="男" v-model="member.sex">男
                                </label>
                                <label class="radio-inline">
                                    <input type="radio" name="sex" value="女" v-model="member.sex"
                                        style="margin-left:15px;">女
                                </label>
                            </div>
                        </div>
                        <div class="form-group">
                            <label>年龄:</label>
                            <input type="number" min="0" class="form-control" v-model="member.age"
                                onkeydown="if(event.keyCode==32){return false;}">
                        </div>
                        <div class="form-group" v-if="modalTitle == '添加一名用户'">
                            <label>密码:</label>
                            <input type="password" class="form-control" v-model="member.password"
                                onkeydown="if(event.keyCode==32){return false;}">
                        </div>
                        <div class="form-group" v-if="modalTitle == '添加一名用户'">
                            <label>重复密码:</label>
                            <input type="password" class="form-control" v-model="confirmPassword"
                                onkeydown="if(event.keyCode==32){return false;}">
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-success" @click="addMember"
                            v-if="modalTitle == '添加一名用户'">添加</button>
                        <button type="button" class="btn btn-primary" @click="updateMember"
                            v-if="modalTitle == '修改一名用户'">修改</button>
                    </div>
                </div>
            </div>
        </div>
        <!-- 添加/修改游艇Modal end -->
    </div>
</template>

<script>
    export default {
        name: 'adminMember',
        data() {
            return {
                member: { //操作增加修改用户
                    id: "",
                    name: "",
                    password: "",
                    tel: "",
                    sex: "男",
                    age: "",
                    level: "普通会员"
                },
                confirmPassword: "",
                member_c: { //存放筛选条件
                    id: "",
                    name: "",
                    tel: "",
                    sex: "-请选择用户性别-",
                    age: "",
                    level: "-请选择用户级别-"
                },
                memberList: [],
                condition: "",
                minAge: "",
                maxAge: "",
                nowPage: 1, //当前页数
                allPage: 0, //总页数
                counts: 9, //一页显示条数
                allCount: 0, //总记录数,
                modalTitle: "添加一名用户"
            }
        },
        methods: {
            getMembers(button) {
                var sex = "";
                if (this.member_c.sex == "-请选择用户性别-") {
                    sex = "";
                } else if (this.member_c.sex == "男") {
                    sex = 0;
                } else if (this.member_c.sex == "女") {
                    sex = 1;
                }
                if (button == "find") { //判断是 "切换页数" 触发该函数,还是 "点击检索" 触发该函数,点击检索会自动切换到第一页
                    this.nowPage = 1;
                }
                var level = "";
                if (this.member_c.level == "-请选择用户级别-") {
                    level = "";
                } else if (this.member_c.level == "普通会员") {
                    level = 0;
                } else if (this.member_c.level == "管理员") {
                    level = 1;
                }
                this.axios({
                    method: "post",
                    url: "/api/member/admin/getMembers.action",
                    data: {
                        condition: this.condition,
                        minAge: this.minAge,
                        maxAge: this.maxAge,
                        sex,
                        level,
                        page: this.nowPage,
                        counts: this.counts
                    },
                    transformRequest: [function (data) { //传递表单信息的post请求
                        // Do whatever you want to transform the data
                        let ret = '';
                        for (let it in data) {
                            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&';
                        }
                        return ret;
                    }],
                }).then((response) => {
                    var map = response.data;
                    if (map.code == 401) { //未登录
                        window.location.href = '/#/admin';
                    } else if (map.code == 200) {
                        if (map.data != null) {
                            var resultArr = JSON.parse(map
                                .data); //返回一个json字符串数组,json[0]是结果集list的json字符串格式,json[1]是一个整数即总记录数
                            this.allCount = resultArr[1]; //取出总记录数
                            this.allPage = parseInt(this.allCount / (this.counts + 1)) + 1; //计算总页数
                            if (this.nowPage > this.allPage) { //判断当前页是否大于最大页数,防止用户筛选完后页数减少,而当前页不显示记录的情况
                                this.nowPage = 1;
                                this.getMembers();
                            } else {
                                this.memberList = JSON.parse(resultArr[0]); //取出分页条件查询结果集渲染页面
                            }
                        } else {
                            this.$emit("showAlert", true, "服务器异常");
                        }
                    }
                }).catch((error) => {
                    console.log(error);
                    if (error.response.status == 401) { //无权限
                        window.location.href = '/#/admin';
                    }
                });
            },
            addMember() { //添加一名用户
                var confi = confirm("确认此操作?");
                if (confi == true) {
                    var role = this.member.level;
                    if (!(/^[1][3,4,5,7,8][0-9]{9}$/.test(this.member.tel)) && role == "普通会员") {
                        this.$emit("showAlert", true, "请输入正确的手机号码");
                    } else if (this.member.name == "") {
                        this.$emit("showAlert", true, "请填写用户姓名");
                    } else if (!(/^\d+$/.test(this.member.tel)) && role == "管理员") {
                        this.$emit("showAlert", true, "管理员账号只能由纯数字构成");
                    } else if (!(/^[0-9]+$/.test(this.member.age))) {
                        this.$emit("showAlert", true, "请输入正确的年龄");
                    } else if (this.member.password.length < 6) {
                        this.$emit("showAlert", true, "密码长度最短六位");
                    } else if (this.confirmPassword != this.member.password) {
                        this.$emit("showAlert", true, "两次输入的密码不一致");
                    } else {
                        if (this.member.sex == "男") {
                            this.member.sex = 0;
                        } else {
                            this.member.sex = 1;
                        }
                        if (role == "普通会员") {
                            this.member.level = 0;
                        } else if (role == "管理员") {
                            this.member.level = 1;
                        }
                        this.axios({
                            url: "/api/member/admin/addMember.action",
                            data: this.member,
                            method: "post"
                        }).then((response) => {
                            var map = response.data;
                            if (map.code == 401) { //未登录
                                window.location.href = '/#/admin';
                            } else if (map.code == 200) {
                                if (map.data == null) { //添加成功
                                    $('#memberModal').modal('hide'); //关闭模态框
                                    this.clearMember();
                                    this.getMembers();
                                    this.$emit("showAlert", true, "操作成功");
                                } else {
                                    if (this.member.sex == 0) {
                                        this.member.sex = "男";
                                    } else {
                                        this.member.sex = "女";
                                    }
                                    if (this.member.level == 0) {
                                        this.member.level = "普通会员";
                                    } else if (this.member.level == 1) {
                                        this.member.level = "管理员";
                                    }
                                    this.$emit("showAlert", true, map.data);
                                }
                            }
                        }).catch((error) => {
                            console.log(error);
                            if (error.response.status == 401) { //无权限
                                window.location.href = '/#/admin';
                            }
                        });

                    }
                }
            },
            updateMember() {
                var confi = confirm("确认此操作?");
                if (confi == true) {
                    var role = this.member.level;
                    if (this.member.name == "") {
                        this.$emit("showAlert", true, "请填写用户姓名");
                    } else if (!(/^[0-9]+$/.test(this.member.age))) {
                        this.$emit("showAlert", true, "请输入正确的年龄");
                    } else {
                        if (this.member.sex == "男") {
                            this.member.sex = 0;
                        } else {
                            this.member.sex = 1;
                        }
                        if (role == "普通会员") {
                            this.member.level = 0;
                        } else if (role == "管理员") {
                            this.member.level = 1;
                        }
                        this.axios({
                            url: "/api/member/updateMemberMsg.action",
                            data: this.member,
                            method: "post"
                        }).then((response) => {
                            var map = response.data;
                            if (map.code == 401) { //未登录
                                window.location.href = '/#/admin';
                            } else if (map.code == 200) {
                                if (map.data == null) { //修改成功
                                    $('#memberModal').modal('hide'); //关闭模态框
                                    this.clearMember();
                                    this.getMembers();
                                    this.$emit("showAlert", true, "操作成功");
                                } else {
                                    this.$emit("showAlert", true, map.data);
                                }
                            }
                        }).catch((error) => {
                            console.log(error);
                            if (error.response.status == 401) { //无权限
                                window.location.href = '/#/admin';
                            }
                        });

                    }
                }
            },
            getMemberById(memberId) {
                this.clearMember();
                if (memberId != "" && memberId != null) {
                    this.axios({
                        url: "/api/member/admin/getMemberById.action",
                        method: "get",
                        params: {
                            id: memberId
                        }
                    }).then((response) => {
                        var map = response.data;
                        if (map.code == 401) { //未登录
                            window.location.href = '/#/admin';
                        } else if (map.code == 200) {
                            var data = JSON.parse(map.data);
                            if (data != null) {
                                this.member = data;
                                if (data.sex == 0) {
                                    this.member.sex = "男";
                                } else {
                                    this.member.sex = "女";
                                }
                                if (data.level == 0) {
                                    this.member.level = "普通会员";
                                } else {
                                    this.member.level = "管理员";
                                }
                            }
                        }
                    }).catch((error) => {
                        console.log(error);
                        if (error.response.status == 401) { //无权限
                            window.location.href = '/#/admin';
                        }
                    });
                }
            },
            deleteMemberById(memberId) {
                if (memberId != null) {
                    var confi = confirm("确定删除该用户");
                    if (confi == true) {
                        this.axios({
                            url: "/api/member/admin/deleteMemberById.action",
                            params: {
                                id: memberId
                            },
                            method: "get"
                        }).then((response) => {
                            var map = response.data;
                            if (map.code == 401) { //未登录
                                window.location.href = '/#/admin';
                            } else if (map.code == 200) {
                                if (map.data == null) {
                                    this.$emit("showAlert", true, "操作成功");
                                    this.getMembers();
                                } else {

                                    this.$emit("showAlert", true, map.data);
                                }
                            } else if (map.code == 400) { //参数错误

                                this.$emit("showAlert", true, "数据传输错误");
                            }
                        }).catch((error) => {
                            console.log(error);
                            if (error.response.status == 401) { //无权限
                                window.location.href = '/#/admin';
                            }
                        });
                    }
                } else {
                    this.$emit("showAlert", true, "数据传输错误");
                }
            },
            clearMember() {
                this.member.id = "";
                this.member.name = "";
                this.member.password = "";
                this.confirmPassword = "";
                this.member.tel = "";
                this.member.sex = "男";
                this.member.age = "";
                this.member.level = "普通会员";
            }
        },
        mounted() {
            this.getMembers();
        },
        created() {
            // this.$emit("ifAdmin");
        }
    }
</script>

<style scoped>

</style>