<template>
    <div id="app" class="container-fluid" ondragstart="return false;">
        <div class="col-12" style="margin-top:10px;">
            <h2 style="color:white;"><b>DockTease后台管理系统</b></h2>
        </div>
        <nav class="navbar navbar-expand-sm bg-light navbar-light">
            <ul class="navbar-nav">
                <li class="nav-item" :class="{active: '/adminMember'===$route.path}">
                    <a onfocus="this.blur();" class="nav-link" href="#" @click.prevent = "$router.push('/adminMember')">管理用户</a>
                </li>
                <li class="nav-item" :class="{active: '/adminYacht'===$route.path}">
                    <a onfocus="this.blur();" class="nav-link" href="#" @click.prevent = "$router.push('/adminYacht')">管理游艇</a>
                </li>
                <li class="nav-item" :class="{active: '/adminOrder'===$route.path}">
                    <a onfocus="this.blur();" class="nav-link" href="#" @click.prevent = "$router.push('/adminOrder')">管理订单</a>
                </li>
                <li class="nav-item" :class="{active: '/adminDisplay'===$route.path}">
                    <a onfocus="this.blur();" class="nav-link" href="#" @click.prevent = "$router.push('/adminDisplay')">管理前台</a>
                </li>
            </ul>
        </nav>
        <transition mode="out-in">
            <router-view v-on:showAlert="showAlert">

            </router-view>
        </transition>

        <div class="div_middle col-12" style="margin-top:5px;">
            <h5>
                <span style="color:white;font-size:15px;">Copyright ©2019 DB-EX WuZhenghua&ChenYuHui</span>
            </h5>
        </div>

        <!-- Alert -->
        <div id="alertModal" class="modal fade" tabindex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle"
            aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">通知</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        {{alertMsg}}
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import "bootstrap/dist/css/bootstrap.css";

    export default {
        name: 'app',
        data() {
            return {
                alertMsg: "通知"
            }
        },
        components: {

        },
        methods: {
            showAlert(ifShow, alertMsg) {
                if (ifShow) {
                    $('#alertModal').modal('show');
                    this.alertMsg = alertMsg;
                } else {
                    $('#alertModal').modal('hide');
                    this.alertMsg = alertMsg;
                }
            }
        },
        created() {
            this.axios({
                url: "/api/user/admin/ifAdmin.action",
                method: "get"
            }).then((response) => {
                var map = response.data;
                if (map.code == 401) {      //未登录
                    window.location.href = '/#/admin';
                }
            }).catch((error) => {
                console.log(error);
                if (error.response.status == 401) {     //无权限
                    window.location.href = '/#/admin';
                }
            });
        }
    }
</script>

<style>
    body {
        background-image: url('../assets/img/common/background_02.png');
        height: 100%;
        width: 100%;
        background-size: cover;
    }

    /* v-enter [这是一个时间点] 进入之前,元素初始状态,此时还没有开始进入 */
    /* v-leave-to [这是一个时间点] 是动画离开之后,离开的终止状态,此时动画已经结束 */
    .v-enter,
    .v-leave-to {
        opacity: 0;
        transform: translateX(1400px);
    }

    /* v-enter-active [入场动画的时间段] */
    /* v-leave-active [离场动画的时间段] */
    .v-enter-active,
    .v-leave-active {
        transition: all 0.4s ease;
    }

    .div_middle {
        text-align: center;
    }

    main {
        text-align: center;
        background-color: linen;
        height: 778px;
        overflow-y: auto;
        overflow-x: hidden;
        opacity: 0.9;
    }
</style>