<template>
    <main id="adminMember">
        <div class="row" style="margin-top:10px;margin-bottom:10px;margin-left:15px;margin-right:15px;">
            <table style="width:100%;">
                <tr>
                    <td rowspan="2" style="width:85px;">
                        <button class="btn btn-success" @click="setVoidOrders">设置无效订单</button>
                    </td>
                    <td style="width:220px;">
                        <button class="btn btn-danger" @click="deleteVoidOrders">删除所有无效订单</button>
                    </td>
                    <td style="width:220px;">
                        <select class="form-control" v-model="consume.state">
                            <option>-请选择订单状态-</option>
                            <option>已支付</option>
                            <option>未支付</option>
                            <option>订单无效</option>
                        </select>
                    </td>
                    <td style="text-align:right;width:140px;" rowspan="2">
                        <label style="margin-right:5px;">游艇使用时间:</label></td>
                    <td style="text-align:left;">
                        <el-date-picker v-model="minUseTime" type="datetime" placeholder="选择最早使用时间"
                            format="yyyy-MM-dd HH:mm" value-format="yyyy-MM-dd HH:mm" @change="getMinUseTime">
                        </el-date-picker>
                    </td>
                    <td style="text-align:right;" rowspan="2">
                        <label style="margin-right:5px;">订单生成时间:</label>
                    </td>
                    <td style="text-align:left;">
                        <el-date-picker v-model="minCreated" type="datetime" placeholder="选择最早订单生成时间"
                            format="yyyy-MM-dd HH:mm" value-format="yyyy-MM-dd HH:mm" @change="getMinCreated">
                        </el-date-picker>
                    </td>
                    <td style="width:600px;" rowspan="2">
                        <div class="input-group">
                            <input type="text" class="form-control" placeholder="消费号/会员编号/联系电话/联系人/游艇编号/订单号/消费金额"
                                v-model="condition">
                            <span class="input-group-btn">
                                <button class="btn btn-info" type="button" @click="getConsumes('find')">检索</button>
                            </span>
                        </div>
                    </td>
                </tr>
                <tr>
                    <td>
                        <button class="btn btn-warning" @click="batchDelConsumes">删除选中无效订单</button>
                    </td>
                    <td>
                        <select class="form-control" v-model="yacht.state">
                            <option>-请选择产品类型-</option>
                            <option>租赁</option>
                            <option>购买</option>
                        </select>
                    </td>
                    <td style="text-align:left;">
                        <el-date-picker v-model="maxUseTime" type="datetime" placeholder="选择最晚使用时间"
                            format="yyyy-MM-dd HH:mm" value-format="yyyy-MM-dd HH:mm" @change="getMaxUseTime">
                        </el-date-picker>
                    </td>
                    <td style="text-align:left;">
                        <el-date-picker v-model="maxCreated" type="datetime" placeholder="选择最晚订单生成时间"
                            format="yyyy-MM-dd HH:mm" value-format="yyyy-MM-dd HH:mm" @change="getMaxCreated">
                        </el-date-picker>
                    </td>
                </tr>
            </table>
        </div>
        <div style="height:594px;">
            <table class="table" style="margin:auto;">
                <thead>
                    <tr>
                        <td>
                            <span class="input-group-addon">
                                <input id="BatchAllCheckbox" type="checkbox" @click="selectBatchDel">
                            </span>
                        </td>
                        <td>消费号</td>
                        <td>所属会员编号</td>
                        <td>联系电话</td>
                        <td>联系人</td>
                        <td>游艇编号</td>
                        <td>包艇时长</td>
                        <td>使用时间</td>
                        <td>产品类型</td>
                        <td>支付方式</td>
                        <td>订单号</td>
                        <td>支付平台订单号</td>
                        <td>订单状态</td>
                        <td>交易金额</td>
                        <td>订单生成时间</td>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="consume in consumeList" :key="consume.id">
                        <td>
                            <span class="input-group-addon">
                                <input type="checkbox" :value="consume.id" v-model="batchDelList"
                                    :disabled="consume.state == 0||consume.state == 1">
                            </span>
                        </td>
                        <td><a>{{consume.id}}</a></td>
                        <td>{{consume.memberId}}</td>
                        <td>{{consume.tel}}</td>
                        <td>{{consume.name}}</td>
                        <td>{{consume.yacht.id}}</td>
                        <td>{{consume.yachtScheme?consume.yachtScheme.time+'小时':'无'}}</td>
                        <td>
                            <span v-if="consume.yacht.state==1">{{consume.useTime|formatDate}}</span>
                            <span v-if="consume.yacht.state==2||consume.yacht.state==3">无</span>
                        </td>
                        <td>{{consume.yachtScheme?'租赁':'购买'}}</td>
                        <td>{{consume.state == 1?'银联支付':'未支付'}}</td>
                        <td><a :href="downLoadOrder+consume.orderId" title="导出订单文件">{{consume.orderId}}</a></td>
                        <td>{{consume.platformorderId?consume.platformorderId:'未完成支付'}}</td>
                        <td>
                            <span v-if="consume.state==1" style="color:green">已支付</span>
                            <span v-if="consume.state==0" style="color:red">未支付</span>
                            <span v-if="consume.state==2" style="color:lightgrey">订单无效</span>
                        </td>
                        <td>￥ {{(consume.price/100)|NumFormat}}</td>
                        <td>{{consume.created|formatDate}}</td>
                    </tr>
                </tbody>
            </table>
        </div>
        <div style="margin-top:2px;">
            <div>共{{allCount}}条记录, {{allPage}}页</div>
            <nav aria-label="Page navigation">
                <ul class="pagination justify-content-center">
                    <li class="page-item" :class="{disabled: nowPage===1}">
                        <a href="#" aria-label="previous" class="page-link">
                            <span @click="nowPage--; getConsumes();">上一页</span>
                        </a>
                    </li>
                    <li v-for="page in allPage" :key="page" class="page-item" :class="{active: nowPage===page}"><a
                            href="#" class="page-link" @click="nowPage = page; getConsumes();"
                            v-if="page>nowPage-5&&page<nowPage+5">{{page}}</a></li>
                    <li class="page-item" :class="{disabled: nowPage===allPage}">
                        <a href="#" aria-label="Next" class="page-link">
                            <span @click="nowPage++; getConsumes();">下一页</span>
                        </a>
                    </li>
                </ul>
            </nav>
        </div>
    </main>
</template>

<script>
    export default {
        name: 'adminMember',
        data() {
            return {
                consume: {
                    id: "",
                    memberId: "",
                    tel: "",
                    name: "",
                    schemeId: "",
                    useTime: "",
                    typeId: "",
                    orderId: "",
                    platformorderId: "",
                    price: "",
                    state: "-请选择订单状态-",
                    payMessage: "",
                    created: "",
                    yacht: "",
                    yachtScheme: ""
                },
                yacht: {
                    id: "",
                    name: "",
                    color: "",
                    length: "",
                    members: "",
                    address: "",
                    state: "-请选择产品类型-"
                },
                yachtScheme: {
                    id: "",
                    yachtId: "",
                    time: "",
                    price: ""
                },
                consumeList: [],
                condition: "", //筛选条件,模糊条件
                minUseTime: "", //筛选条件,最小使用时间
                maxUseTime: "", //筛选条件,最大使用时间
                minCreated: "", //筛选条件,最小创建时间
                maxCreated: "", //筛选条件,最大创建时间
                nowPage: 1, //当前页数
                allPage: 0, //总页数
                counts: 11, //一页显示条数
                allCount: 0, //总记录数
                batchDelList: [] //保存选中checkbox的消费id
            }
        },
        methods: {
            getConsumes(button) {
                this.batchDelList = []; //清空checkbox选中状态
                $("#BatchAllCheckbox").prop("checked", false); //设置全选框为未选中状态

                var state = "";
                if (this.consume.state == "-请选择订单状态-") {
                    state = "";
                } else if (this.consume.state == "已支付") {
                    state = 1;
                } else if (this.consume.state == "未支付") {
                    state = 0;
                } else if (this.consume.state == "订单无效") {
                    state = 2;
                }
                var productType = "";
                if (this.yacht.state == "租赁") {
                    productType = 1;
                } else if (this.yacht.state == "购买") {
                    productType = 2;
                } else if (this.yacht.state == "-请选择产品类型-") {
                    productType = "";
                }
                if (button == "find") { //判断是 "切换页数" 触发该函数,还是 "点击检索" 触发该函数,点击检索会自动切换到第一页
                    this.nowPage = 1;
                }
                if (this.minUseTime == null) {
                    this.minUseTime = "";
                }
                if (this.maxUseTime == null) {
                    this.maxUseTime = "";
                }
                if (this.minCreated == null) {
                    this.minCreated = "";
                }
                if (this.maxCreated == null) {
                    this.maxCreated = "";
                }

                this.axios({
                    method: "post",
                    url: "/api/consume/admin/getConsumes.action",
                    data: {
                        condition: this.condition,
                        state,
                        productType,
                        minUseTime: this.minUseTime,
                        maxUseTime: this.maxUseTime,
                        minCreated: this.minCreated,
                        maxCreated: this.maxCreated,
                        page: this.nowPage,
                        counts: this.counts
                    },
                    transformRequest: [function (data) { //传递表单信息的post请求
                        // Do whatever you want to transform the data
                        let ret = '';
                        for (let it in data) {
                            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&';
                        }
                        return ret;
                    }],
                }).then((response) => {
                    var map = response.data;
                    if (map.code == 401) { //未登录
                        window.location.href = '/#/admin';
                    } else if (map.code == 200) {
                        var data = JSON.parse(map.data);
                        if (data != null) {
                            var consumeJson = data; //返回一个json字符串数组,json[0]是结果集list的json字符串格式,json[1]是一个整数即总记录数
                            this.allCount = consumeJson[1]; //取出总记录数
                            this.allPage = parseInt(this.allCount / (this.counts + 1)) + 1; //计算总页数
                            if (this.nowPage > this.allPage) { //判断当前页是否大于最大页数,防止用户筛选完后页数减少,而当前页不显示记录的情况
                                this.nowPage = 1;
                                this.getConsumes();
                            } else {
                                this.consumeList = JSON.parse(consumeJson[0]); //取出分页条件查询结果集渲染页面
                            }
                        }
                    } else {
                        console.log(map);
                    }
                }).catch((error) => {
                    console.log(error);
                    if (error.response.status == 401) { //无权限
                        window.location.href = '/#/admin';
                    }
                });
            },
            getMinUseTime(val) {
                this.minUseTime = val;
            },
            getMaxUseTime(val) {
                this.maxUseTime = val;
            },
            getMinCreated(val) {
                this.minCreated = val;
            },
            getMaxCreated(val) {
                this.maxCreated = val;
            },
            selectBatchDel() {
                if ($("#BatchAllCheckbox").prop('checked')) {
                    for (var i = 0; i < this.consumeList.length; ++i) {
                        if (this.consumeList[i].state == 2) { //若为过期订单,checkbox选上
                            this.batchDelList.push(this.consumeList[i].id);
                        }
                    }
                } else {
                    this.batchDelList = [];
                }
            },
            batchDelConsumes() {
                var confi = confirm("确认删除选中的无效订单?");
                if (confi == true) {
                    this.axios({
                        method: "post",
                        url: "/api/consume/admin/deleteNullConsumes.action",
                        data: this.batchDelList
                    }).then((response) => {
                        var map = response.data;
                        if (map.code == 401) {  //未登录
                            window.location.href = '/#/admin';
                        } else if (map.code == 200) {
                            if (map.data == null) {
                                this.getConsumes("find");
                                this.$emit("showAlert", true, "操作成功");
                            } else {
                                this.$emit("showAlert", true, map.data);
                            }
                        } else{
                            console.log(map);
                        }
                    }).catch((error) => {
                        console.log(error);
                        if (error.response.status == 401) { //无权限
                            window.location.href = '/#/admin';
                        }
                    });
                }
            },
            deleteVoidOrders() {
                var confi = confirm("确定删除所有无效订单吗");
                if (confi == true) {
                    this.axios({
                        url: "/api/consume/admin/deleteVoidOrders.action",
                        method: "get"
                    }).then((response) => {
                        var map = response.data;
                        if(map.code == 401){//未登录
                            window.location.href = '/#/admin';
                        }else if(map.code == 200){
                            if(map.data == null){
                                this.getConsumes();
                                this.$emit("showAlert", true, "操作成功");
                            }
                        }else{
                            console.log(map);
                        }
                    }).catch((error) => {
                        console.log(error);
                        if (error.response.status == 401) { //无权限
                            window.location.href = '/#/admin';
                        }
                    })
                }
            },
            setVoidOrders() {
                var confi = confirm("设置所有超时未支付订单为无效订单,是否继续");
                if (confi == true) {
                    this.axios({
                        url: "/api/consume/admin/setVoidOrders.action",
                        method: "get"
                    }).then((response) => {
                        var map = response.data;
                        if(map.code == 401){
                            window.location.href = '/#/admin';
                        }else if(map.code == 200){
                            if(map.data == null){
                                this.$emit("showAlert", true, "操作成功");
                                this.getConsumes();
                            }else{
                                this.$emit("showAlert", true, map.data);
                            }
                        }else{
                            console.log(map);
                        }
                    }).catch((error) => {
                        console.log(error);
                        if (error.response.status == 401) { //无权限
                            window.location.href = '/#/admin';
                        }
                    });
                }
            }
        },
        mounted() {
            this.getConsumes();
        },
        filters: {
            formatDate(dataStr) { //时间戳抓日期格式
                var time = new Date(dataStr);

                function timeAdd0(str) {
                    if (str < 10) {
                        str = '0' + str;
                    }
                    return str
                }
                var y = time.getFullYear();
                var m = time.getMonth() + 1;
                var d = time.getDate();
                var h = time.getHours();
                var mm = time.getMinutes();
                var s = time.getSeconds();
                return y + '-' + timeAdd0(m) + '-' + timeAdd0(d) + ' ' + timeAdd0(h) + ':' + timeAdd0(mm);
            },
            NumFormat(value) { //三位一逗号
                if (!value) return '0.00';

                /*原来用的是Number(value).toFixed(0)，这样取整时有问题，例如0.51取整之后为1，感谢Nils指正*/
                var intPart = Number(value) | 0; //获取整数部分
                var intPartFormat = intPart.toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,'); //将整数部分逢三一断

                var floatPart = ".00"; //预定义小数部分
                value = value.toString();
                var value2Array = value.split(".");

                //=2表示数据有小数位
                if (value2Array.length == 2) {
                    floatPart = value2Array[1].toString(); //拿到小数部分

                    if (floatPart.length == 1) { //补0,实际上用不着
                        return intPartFormat + "." + floatPart + '0';
                    } else {
                        return intPartFormat + "." + floatPart;
                    }

                } else {
                    return intPartFormat + floatPart;
                }
            }
        }
    }
</script>

<style scoped>

</style>